import { ApplianceCommandCode } from '../enums/ApplianceCommandCode';
import { CommandStatusCode } from '../enums/CommandStatusCode';
import { ComponentConfiguration } from '../entities/ComponentConfiguration';
import { BaseEntity } from '../entities/BaseEntity';

/* 
   Appliance command - represents commands sent to the appliance 
*/
export class ApplianceCommand extends BaseEntity {
 
    // Account Id 
    public accountId: string;
 
    // Appliance Id 
    public applianceId: string;
 
    // Operation code: UNDEFINED | GET_CAPABILITIES | SET_COMPONENTS | REMOVE_COMPONENTS | RESTART_COMPONENTS | LIST_CONTAINERS ... 
    public opCode: ApplianceCommandCode;
 
    // Command status flag: PENDING | EXECUTING | COMPLETED | FAILED 
    public status: CommandStatusCode;
 
    // The failure reason in case of FAILED status (4) 
    public reason: string;
 
    // Components to add (payload for SET_COMPONENTS op-code) 
    public components: ComponentConfiguration[];
 
    // List of component Ids to remove or reset (payload for REMOVE_COMPONENTS, RESTART_COMPONENTS op-codes) 
    public componentIds: string[];
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


