import { SensorStatus } from '../entities/SensorStatus';
import { WebSocketMessageHeader } from '../messages/WebSocketMessageHeader';

/* 
   @WebSocketMessage
   This message is pushed to the client upon any sensor health status change in the registered account 
*/
export class SocketHealthStatusNotification extends WebSocketMessageHeader {
 
    // Error code (0 for success) 
    public code: number;
 
    // Error message (if error occurred) 
    public error: string;
 
    // Sensor status 
    public sensorStatus: SensorStatus;
 
    // 
    public op: number;
 
    // 
    public ver: number;
 
    // 
    public id: number;
 
    // 
    public cid: number;
 
    // 
    public sid: string;
 

}


