import { WebSocketMessageHeader } from '../messages/WebSocketMessageHeader';

/* 
   @WebSocketMessage
   This message is pushed to the client upon any new event in the registered account 
*/
export class SocketEventNotification extends WebSocketMessageHeader {
 
    // Error code (0 for success) 
    public code: number;
 
    // Error message (if error occurred) 
    public error: string;
 
    // Event account Id 
    public accountId: string;
 
    // Event sensor Id 
    public sensorId: string;
 
    // Event Id 
    public eventId: string;
 
    // 
    public op: number;
 
    // 
    public ver: number;
 
    // 
    public id: number;
 
    // 
    public cid: number;
 
    // 
    public sid: string;
 

}


